import { API_URI } from '../constants'

const services = {
  login: API_URI + '/auth/login',
  register: API_URI + '/auth/register',
}

export async function apiAuth(params: any) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
    body: JSON.stringify(params),
  }
  let res = null

  await fetch(services.login, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function apiUserRegister(params: any) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
    body: JSON.stringify(params),
  }
  let res = null

  await fetch(services.register, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}
